.gasa-app-button {
  text-transform: uppercase;
  font-size: $medium-font-title-size;
  line-height: 1.3;
  padding: 1em;
  border-radius: $base-border-radius;
  background-color: $base-yellow;
  cursor: pointer;
  width: 100%;
  margin: 1em 0;

  &:hover {
    background-color: transparent;
  }

  &--with-pulse {
    animation: 1s wiggle ease 10s 10;
  }

  &--invisible {
    display: none;
  }
}
